@import "vars";
.page {
    overflow-x: hidden;
    width: 100%;
    background: #F9FAFF;
    min-height: 100vh;
}

body {
    font-family: 'Inter', sans-serif !important;
    font-weight: 400;
    color: $black;
    letter-spacing: 0.02em;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    position: relative;
    height: 100%;
    width: 100%;
    // overflow-x: hidden;
}

.content {
    flex-grow: 1;
}

.container {
    max-width: 1700px;
    margin: 0 auto;
    padding: 0 20px;
    &--small {
        max-width: 1420px;
    }
    @media screen and (max-width: 1600px) {
        // max-width: 1200px;
    }
}